.footer {
    background-color: #202020;
}

.footer__container {
    max-width: calc((1140 / 1280) * 100%);
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}

.footer__text {
    margin: 0px;
    font-size: 13px;
    font-weight: 400;
    font-style: normal;
    line-height: 16px;
    color: #8B8B8B;
    padding: 79px 0px 20px 0px;
    border-bottom: 1px solid #424242;
    text-align: center;
}

.footer__content-copyright {
    display: flex;
    justify-content: space-between;
}

.footer__copyrigh {
    margin: 0px;
    font-size: 13px;
    font-weight: 400;
    font-style: normal;
    line-height: 16px;
    color: #FFFFFF;
    padding: 20px 0px;
}

.footer__items {
    list-style: none;
    margin: 0px;
    padding: 20px 0px;
    display: flex;
}

.footer__item {
    padding-left: 20px;
}

.footer__link {
    font-size: 13px;
    font-weight: 400;
    font-style: normal;
    line-height: 16px;
    color: #FFFFFF;
    text-decoration: none;
}

.footer__link:hover {
    transition: opacity 1s ease;
    opacity: 0.8;
}

@media (max-width: 1075px) {
    .footer__container {
        max-width: calc((668 / 768) * 100%);
    }
}

@media (max-width: 600px) {
    .footer__container {
        max-width: calc((300 / 320) * 100%);
    }

    .footer__text {
        font-size: 12px;
        line-height: 15px;
    }

    .footer__copyrigh {
        font-size: 12px;
        line-height: 15px;
        color:  #8B8B8B;
        padding: 18px 0px 10px 0px;
    }

    .footer__content-copyright {
        flex-direction: column-reverse;
        align-items: center;
    }

    .footer__items {
        flex-direction: column;
        align-items: center;
        padding:30px 0px 0px  0px;
    }

    .footer__item {
        padding: 0px 0px 12px 0px;
    }

    .footer__link {
        font-size: 12px;
        line-height: 15px;
    }
}